<script>export var size = 25;
export var speed = 750;
export var color = "rgba(0,0,0,0.4)";
export var thickness = 2;
export var gap = 40;
export var radius = 10;
var dash;

$: dash = 2 * Math.PI * radius * (100 - gap) / 100;</script>

<style>
  .svelte-spinner {
    transition-property: transform;
    animation-name: svelte-spinner_infinite-spin;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
  }
  @keyframes svelte-spinner_infinite-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
</style>

<!-- Ty EmilTholin for creating a high quality open source spinner -->
<!-- https://github.com/EmilTholin/svelte-spinner/blob/master/src/index.svelte -->
<svg height={size} width={size} style="animation-duration:{speed}ms;" class="svelte-spinner" viewbox="0 0 32 32">
  <circle
    role="presentation"
    cx="16"
    cy="16"
    r={radius}
    stroke={color}
    fill="none"
    stroke-width={thickness}
    stroke-dasharray="{dash},100"
    stroke-linecap="round" />
</svg>
